.btn_Handler {
  border: none;
  border-radius: 6px;
  background-image: linear-gradient(
    to top,
    rgba(211, 111, 72, 0.925),
    rgb(141, 81, 46)
  );
  /* ---jugar with buttons rgba-- */
  color: rgb(255, 255, 255);
  margin: 0.5rem;
  padding: 10px;
  font-weight: bold;
  box-shadow: 2px 3px 8px 0px rgba(0, 0, 0, 0.8);
}
.btn_Handler:hover {
  transform: scale(1.02);
  transition: all 250ms;
}
