/* Todo esto puede ser un background linear gradient */

.Button__home {
  border: none;
  border-radius: 5px;
  background-image: linear-gradient(
    to top,
    rgba(85, 54, 42, 0.925),
    rgb(145, 93, 62)
  );
  /* ---jugar with buttons rgba-- */
  color: rgb(255, 255, 255);
  padding: 10px;
  font-weight: bold;
  box-shadow: 2px 3px 8px 0px rgba(0, 0, 0, 0.8);
  text-align: center;
}

.container-home {
  max-width: 1200px;
  margin: auto;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
}

.col-2 {
  flex-basis: 35%;
  min-width: 300px;
}
.col-2 img {
  max-width: 100%;
  padding: 50px 0;
}

.col-2 h1 {
  font-size: 3.2rem;
  line-height: 50px;
  margin: 15px 0;
}
