}

.Option::after {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  -webkit-transform: translateX(+5px);
          transform: translateX(+5px);

  content: "";
  display: block;
  width: 0%;
  border-bottom: solid 2px white;
  opacity: 0;
}
.Option:hover::after {
  opacity: 1;
  width: 85%;
  transition: all 500ms;
}
