  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: inherit;
  height: 135px;
}

.itemCount__buttons {
  padding-bottom: 1.5rem;
}

.button__count {
  border: none;
  color: #fff;
  border-radius: 50px;
  font-size: 1.25rem;
  font-weight: bolder;
  width: 30px;
  height: 30px;
  background-color: rgb(117, 128, 143);
}
.button__count:hover:enabled {
  background-color: white;
  border: 1px solid rgb(178, 212, 250);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  transition: ease-in-out all 200ms;
  color: rgb(117, 128, 143);
  box-shadow: 0px 0px 5px 0px rgb(0, 0, 0);
}

.addToCart_btn {
  border: none;
  border-radius: 5px;
  background-image: linear-gradient(
    to top,
    rgb(69, 97, 133),
    rgb(117, 127, 157)
  );
  color: rgb(255, 255, 255);
  padding: 0.5rem 0.5rem;
  font-weight: bold;
  box-shadow: 2px 3px 8px 0px rgba(0, 0, 0, 0.8);
}
.addToCart_btn:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: all 250ms;
}
.parrafo__count {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 57px;
}

.card__img_detail {
  padding: 0.3rem;
  width: 35%;
  height: auto;
  border-radius: 0.8rem;
  object-fit: contain;
}

.details_details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  border-right: 2px solid rgb(180, 180, 180);
  border-left: 1px solid rgb(180, 180, 180);
}

.description_detail {
  font-size: 1.255rem;
  padding: 0.7rem;
  font-weight: 600;
}

.footer__detail {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.itemDetail {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 80%;
  height: 100%;
  border-radius: 10px;
  padding: 3rem;
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.288);
  background-image: linear-gradient(rgb(250, 250, 250), rgb(235, 235, 235));
}

.title_detail {
  color: rgb(84, 68, 78);
  margin: 1rem 1rem;
  text-align: center;
  text-decoration: underline;
}

.mainDetail {
  box-sizing: border-box;
  display: flex;
  align-items: stretch;
  width: 100%;
  justify-content: space-evenly;
  padding: 1.5rem;
}
.price_detail {
  color: rgb(84, 68, 78);
  font-weight: bold;
  font-size: 1.5rem;
  margin: 0 auto;
  text-align: center;
}

